














































{ Query } = require "~plugins/memory-record"
BrowserValue = require "~plugins/browser-value"
_ = require "lodash"

q = new BrowserValue
q.query
  order: "story_length"
  tag_id:  "all"

module.exports =
  default:
    watch: q.watch ->
    data: ->
      q.data @

    mounted: ->
      @$store.dispatch "aggregate/faces"

    computed:
      faces: ->
        asc =
          switch @order
            when "order", "date_min"
              "asc"
            else
              "desc"
        { read_at } = @$store.state.aggregate
        Query.faces.tag(@tag_id).sort(@order, asc)





